import React, { useEffect, useRef, useState } from 'react';
import { BoxSeam, Cash, ChevronLeft, Download } from 'react-bootstrap-icons';
import { Helmet } from 'react-helmet-async';
import { Link, useNavigate, useParams, useSearchParams } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import clsx from 'clsx';
import { Button } from 'primereact/button';
import { useDebounce } from 'primereact/hooks';
import { toast } from 'sonner';
import VehicleHistoryTable from './vehicle-history-table';
import style from './vehicle-history.module.scss';
import { getVehicle } from '../../../../APIs/assets-api';
import NewExpensesCreate from '../../../../components/Business/features/expenses-features/new-expenses-create/new-expense-create';
import { CreateNewService } from '../../../../features/business/assets/create-new-service/create-new-service';

const VehicleHistory = () => {
    const { id } = useParams();
    const navigate = useNavigate();
    const [searchParams] = useSearchParams();
    const dt = useRef(null);
    const [selected, setSelected] = useState(null);
    const [inputValue, debouncedValue, setInputValue] = useDebounce('', 400);
    const [showServiceModal, setShowServiceModal] = useState(false);
    const [showExpenseModal, setShowExpenseModal] = useState(false);
    const [refetch, setRefetch] = useState(false);
    const [filterType, setFilterType] = useState('all'); // 'all', 'services', 'expenses'

    // Initialize filterType from URL params
    useEffect(() => {
        const filterParam = searchParams.get('filterType');
        if (filterParam && ['all', 'services', 'expenses'].includes(filterParam)) {
            setFilterType(filterParam);
        }
    }, [searchParams]);

    // Fetch vehicle details
    const vehicleQuery = useQuery({
        queryKey: ['vehicle', id],
        queryFn: () => getVehicle(id),
        enabled: !!id,
        retry: 1,
        cacheTime: 0
    });

    const exportCSV = (selectionOnly) => {
        if (dt.current) {
            dt.current.exportCSV({ selectionOnly });
        } else {
            console.error('DataTable ref is null');
        }
    };

    if (vehicleQuery?.error?.message === "Not found") {
        toast.error('Vehicle not found');
        navigate('/assets?type=vehicles');
    }

    return (
        <>
            <Helmet>
                <title>History - {vehicleQuery?.data?.registration_number || 'Vehicle'}</title>
            </Helmet>

            <div className='vehicle-history-page'>
                <div className='vehicle-history' style={{ width: '100%' }}>
                    <div className={`topbar ${selected?.length ? style.active : ''}`} style={{ padding: '4px 32px 4px 23px', position: 'relative', height: '48px' }}>
                        <div className='left-side d-flex align-items-center' style={{ gap: '16px' }}>
                            {
                                selected?.length ? (
                                    <>
                                        <h6 className={style.selectedCount}>Selected: {selected?.length}</h6>
                                        <div className='filtered-box'>
                                            <button className={`${style.filterBox}`} onClick={() => exportCSV(true)}><Download /></button>
                                        </div>
                                    </>
                                ) : (
                                    <>
                                        <Link to={"/assets?type=vehicles"} className={`${style.gobackBtn}`}>
                                            <ChevronLeft color='#106b99' size={20} /> Go Back
                                        </Link>
                                        <div className="searchBox" style={{ position: 'relative' }}>
                                            <div style={{ position: 'absolute', top: '2px', left: '6px' }}>
                                                <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20" fill="none">
                                                    <path d="M14.6777 12.9299C15.6661 11.5841 16.25 9.92275 16.25 8.125C16.25 3.63769 12.6123 0 8.125 0C3.63769 0 0 3.63769 0 8.125C0 12.6123 3.63769 16.25 8.125 16.25C9.92323 16.25 11.585 15.6658 12.9309 14.6769L12.9299 14.6777C12.9667 14.7277 13.0078 14.7756 13.053 14.8208L17.8661 19.6339C18.3543 20.122 19.1457 20.122 19.6339 19.6339C20.122 19.1457 20.122 18.3543 19.6339 17.8661L14.8208 13.053C14.7756 13.0078 14.7277 12.9667 14.6777 12.9299ZM15 8.125C15 11.922 11.922 15 8.125 15C4.32804 15 1.25 11.922 1.25 8.125C1.25 4.32804 4.32804 1.25 8.125 1.25C11.922 1.25 15 4.32804 15 8.125Z" fill="#98A2B3" />
                                                </svg>
                                            </div>
                                            <input
                                                type="text"
                                                placeholder="Search"
                                                value={inputValue}
                                                onChange={(e) => setInputValue(e.target.value)}
                                                className="border search-resource"
                                                style={{
                                                    borderRadius: '4px',
                                                    width: '184px',
                                                    border: '1px solid #D0D5DD',
                                                    color: '#424242',
                                                    paddingLeft: '36px',
                                                    fontSize: '14px',
                                                    height: '32px'
                                                }}
                                            />
                                        </div>
                                    </>
                                )
                            }
                        </div>

                        <div className="featureName d-flex align-items-center gap-3" style={{ position: 'absolute', left: '50%', top: '50%', transform: 'translate(-50%, -50%)' }}>
                            <Link 
                                to={`/assets/vehicles/${id}/history`} 
                                className={clsx('d-flex align-items-center px-2 py-1', style.subMenuLink, { [style.activeTab]: filterType === 'all' })}
                                onClick={() => setFilterType('all')}
                            >
                                <span className={style.topBarText}>All</span>
                            </Link>

                            <Link 
                                to={`/assets/vehicles/${id}/history`} 
                                className={clsx('d-flex align-items-center px-2 py-1', style.subMenuLink, { [style.activeTab]: filterType === 'services' })}
                                onClick={() => setFilterType('services')}
                            >
                                <BoxSeam color='#1AB2FF' size={16} className='me-2' />
                                <span className={style.topBarText}>Services</span>
                            </Link>

                            <Link 
                                to={`/assets/vehicles/${id}/history`} 
                                className={clsx('d-flex align-items-center px-2 py-1', style.subMenuLink, { [style.activeTab]: filterType === 'expenses' })}
                                onClick={() => setFilterType('expenses')}
                            >
                                <Cash color='#F79009' size={16} className='me-2' />
                                <span className={style.topBarText}>Expenses</span>
                            </Link>
                        </div>

                        <div className="right-side d-flex align-items-center" style={{ gap: '8px' }}>
                            <Button onClick={() => setShowExpenseModal(true)} className={`${style.newButton} ${style.outlineButton}`}>
                                New Expense
                            </Button>
                            <Button onClick={() => setShowServiceModal(true)} className={`${style.newButton}`}>
                                New Service
                            </Button>
                        </div>
                    </div>

                    <VehicleHistoryTable
                        ref={dt}
                        selected={selected}
                        setSelected={setSelected}
                        searchValue={debouncedValue}
                        refetch={refetch}
                        filterType={filterType}
                    />
                </div>
            </div>

            {/* Create New Service Modal */}
            <CreateNewService
                visible={showServiceModal}
                setVisible={setShowServiceModal}
                setRefetch={setRefetch}
                vehicleId={id}
            />

            {/* Create New Expense Modal */}
            {showExpenseModal && (
                <NewExpensesCreate
                    visible={showExpenseModal}
                    setVisible={setShowExpenseModal}
                    setRefetch={setRefetch}
                    assetForExpense={{ id: +id, type: 1 }}
                />
            )}
        </>
    );
};

export default VehicleHistory;
